.info-card {
  position: absolute;
  background-color: transparent;
  border: none;
  margin-left: -148px;
  top: 476px;
  margin-top: -219px;
  z-index: 200;
  display: none;
  max-width: 25rem;
  min-width: 22rem;
  box-sizing: border-box;
  border-radius: 3px;
  padding: 15px .2rem 10px;
  background-color: RGB(255,255,255);
  box-shadow: -3px 0 3px #d1d1d1,4px 0 4px #d1d1d1,0 -1px 1px #d1d1d1,-4px 6px 6px #d1d1d1;
  -webkit-transform: translateX(-200px) translateY(-300px);
          transform: translateX(-200px) translateY(-300px);
}

.info-card .header {
  font-size: 20px;
  color: #000;
  font-weight: 700
}

.icons {
  position: relative;
  top: .5rem;
}

.icons img {
  width: 25px;
  height: 25px;
  margin: 0 .5rem;
}

.container .info-container {
  margin-top: .5rem;
  padding: .5rem;
}

.close {
  position: absolute;
  top: 0;
  right: 0;
}
table {
    margin-top: .6rem;
    width: 100%;
}

table th:first-child {
    width: 40%;
}

table th, table td {
    text-align: center;
}

table th:not(:first-child) {
    width: 30%;
}

tbody {
    font-size: 14px;
}

.line-name {
    min-width: 5rem;
    text-align: center;
    height: 26px;
    line-height: 28px;
    padding: 0 5px;
    display: inline-block;
}

.line {
    display: flex;
    flex-direction: row;
    margin-top: .5rem;
}

.initial-line {
    background: #eee;
    color: #777;
    font-weight: 400
}

.timesheet {
    display: block;
}
.svg {
  font-size: 12px;
  font-family: 微软雅黑;
  width: 2000px;
  height: 2000px;
  -webkit-transform: translateX(-200px) translateY(-300px);
          transform: translateX(-200px) translateY(-300px);
}
